import React, { Component } from "react";
import "./mypost.css";
import { Link, Route, BrowserRouter } from "react-router-dom";

class MyPost extends Component {
  state = {
    current_id: this.props.id,
    mypost: [],
  };

  handleDelete = (post_id) => {
    fetch("/create/deletepost", {
      method: "DELETE",
      headers: {
        "Content-Type": "application/json",
      },
      credentials: "same-origin",
      body: JSON.stringify({
        id: this.state.current_id,
        post_id: post_id,
      }),
    }).then(
      fetch("/post/mypost", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        credentials: "same-origin",
        body: JSON.stringify({
          id: this.state.current_id,
        }),
      })
        .then((res) => res.json())
        .then((data) => this.setState({ mypost: data })).catch(err=>{console.log(err)})
    );
  };

  componentDidMount() {
    fetch("/post/mypost", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      credentials: "same-origin",
      body: JSON.stringify({
        id: this.state.current_id,
      }),
    })
      .then((res) => res.json())
      .then((data) => this.setState({ mypost: data })).catch(err=>{console.log(err)});
  }
  render() {
    const myposts = this.state.mypost.map((post) => {
      return (
        <div className='mypost'>
          <div className='mypost_image_container'>
            <img src={`http://localhost:8000/${post.productImage}`} />
          </div>
          <div className='mypost_text_container'>
            <div className='mypost_text_container_heading'>
              <div>
                <Link to={{ pathname: `/post/${post._id}` }}>
                  {post.Heading}
                </Link>
              </div>
            </div>
            <div className='mypost_text_container_post_content'>
              <p>{post.Post_Content}</p>
              <div>Read More</div>
            </div>

            <div className='mypost_text_container_footer'>
              <div>
                <Link
                  to={{
                    pathname: "/editpost",
                    state: {
                      Heading: post.Heading,
                      Post_Content: post.Post_Content,
                      Post_id: post._id,
                    },
                  }}>
                  Edit
                </Link>
                <button onClick={() => this.handleDelete(post._id)}>
                  Delete
                </button>
              </div>
              <div>Published/Modified On: {post.Published_Date}</div>
            </div>
          </div>
        </div>
      );
    });
    return <div>{myposts}</div>;
  }
}

export default MyPost;
