import React, { Component } from "react";
import { BrowserRouter, Route, Link, Redirect, Switch } from "react-router-dom";

import LpImage from "./Icons_and_Images/LP1.jpg";
import Logo from "./Icons_and_Images/logo.png";

import "./landing.css";

class Landing extends Component {
  render() {
    if (this.props.isauthenticated) {
      return null;
    } else {
      return (
        <div className='landing_container'>
          <div>
            <img src={LpImage} alt='Logo' className='LpImage' />
          </div>
          <div className='landing_login_register'>
            <Link to='/login'> Login</Link>
            <Link to='/register'> Register</Link>
          </div>
          <div className='landing_logo'>
            <img src={Logo} alt='Logo' />
          </div>
          <div className='landing_welcome'>
            <h1>Welcome to Muguser </h1>
          </div>
          <div className='landing_message'>
            <h2>Dive deeper on topics that matter to you.</h2>
            <p>
              Muguser is not like any other platform on the internet. Our sole
              purpose is to help you find compelling ideas, knowledge, and
              perspectives. We don’t serve ads—we serve you, the curious reader
              who loves to learn new things. Muguser is home to thousands of
              independent voices, and we combine humans and technology to find
              the best reading for you—and filter out the rest.
            </p>
          </div>
        </div>
      );
    }
  }
}

export default Landing;
