import React, { Component } from "react";
import "./allposts.css";

import { Link } from "react-router-dom";

class Allpost extends Component {
  state = {
    current_id: this.props.id,
    allpost: [],
  };
  componentDidMount() {
    fetch("/post/allpost", {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
      },
      credentials: "same-origin",
    })
      .then((res) => res.json())
      .then((data) => this.setState({ allpost: data })).catch(err=>{console.log(err)});
  }

  handlefollow = (post_author) => {
    fetch("/connection/follow", {
      method: "POST",
      headers: {
        "content-Type": "application/json",
      },
      credentials: "same-origin",
      body: JSON.stringify({
        req_from: this.state.current_id,
        req_to: post_author,
      }),
    }).then((data) => console.log(data)).catch(err=>{console.log(err)});
  };

  render() {
    const allpost = this.state.allpost.map((post) => {
      return (
        <div className='allpost'>
          <div className='container'>
            Author Name:
            <button onClick={() => this.handlefollow(post.Author)}>
              Follow
            </button>
          </div>
          <div className='container'>
            <Link to={{ pathname: `/post/${post._id}` }}>{post.Heading}</Link>
          </div>
          <div className='container'>
            <p>{post.Post_Content}</p>
            <Link to={{ pathname: `/post/${post._id}` }}>Read More</Link>
          </div>

          <div className='container'>
            Published/Modified On: {post.Published_Date}
          </div>
        </div>
      );
    });

    return (
      <div>
        <div> {allpost} </div>
      </div>
    );
  }
}

export default Allpost;
