import React, { Component } from "react";
import "./importantpost.css";

import { Link } from "react-router-dom";

class Importantpost extends Component {
  state = {
    Impost: [],
    current_id: this.props.id,
  };

  componentDidMount() {
    fetch("/post/imppost", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      credentials: "same-origin",
      body: JSON.stringify({
        id: this.state.current_id,
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        this.setState({ Impost: data });
      }).catch(err=>{console.log(err)});
  }

  render() {
    const Imposts = this.state.Impost.map((post) => {
      return (
        <div className='importantpost'>
          <div className='container'>
            Author Name:
            <button>Follow</button>
          </div>
          <div className='container'>
            <Link to={{ pathname: `/post/${post._id}` }}>{post.Heading}</Link>
          </div>
          <div className='container'>
            <p>{post.Post_Content}</p>
            <p>Read More</p>
          </div>

          <div className='footer_container'>
            Published/Modified On: {post.Published_Date}
          </div>
        </div>
      );
    });

    return <div>{Imposts}</div>;
  }
}

export default Importantpost;
