import React, { Component } from "react";
import { Link } from "react-router-dom";
import "./navbar.css";

class Navbar extends Component {
  state = {
    display: "none",
    transition: "1000ms",
  };
  handlehover = (e) => {
    this.setState({ display: "block" });
  };

  handlehover1 = (e) => {
    this.setState({ display: "none" });
  };

  handlelogout = () => {
    fetch("/user/logout").then((data) => this.props.handlelogout(data)).catch(err=>{console.log(err)});
  };

  render() {
    if (this.props.isauthenticated) {
      return (
        <div className='navbar'>
          <div className='navbar_ul_left'>
            <div className='navbar_home'>
              <Link to='/home'> Home</Link>
            </div>
            <div className='navbar_posts'>
              <Link
                className='navbar_post'
                to='/posts'
                onMouseOver={this.handlehover}
                onMouseOut={this.handlehover1}
                name='posts'>
                Posts
              </Link>
              <Link
                className='navbar_mypost'
                to='/mypost'
                name='mypost'
                onMouseOver={this.handlehover}
                onMouseOut={this.handlehover1}
                style={{
                  display: this.state.display,
                  transition: this.state.transition,
                }}>
                MyPost
              </Link>
              <Link
                className='navbar_importantpost'
                to='/importantpost'
                name='importantpost'
                onMouseOver={this.handlehover}
                onMouseOut={this.handlehover1}
                style={{ display: this.state.display }}>
                Important Post
              </Link>
              <Link
                className='navbar_allpost'
                to='/allpost'
                name='allposts'
                onMouseOver={this.handlehover}
                onMouseOut={this.handlehover1}
                style={{ display: this.state.display }}>
                All Post
              </Link>
            </div>
            <div className='navbar_create_post'>
              <Link to='/createpost'> Create Post</Link>
            </div>
          </div>
          <div className='navbar_ul_right'>
            <div className='navbar_username'>
              <Link to='/mypost'> Hi {this.props.name}</Link>
            </div>

            <div className='navbar_logout'>
              <Link onClick={this.handlelogout}> Logout</Link>
            </div>
          </div>
        </div>
      );
    } else {
      return null;
    }
  }
}

export default Navbar;
