import React, { Component } from "react";
import "./home.css";
import { Link, Route, BrowserRouter } from "react-router-dom";

class Home extends Component {
  render() {
    return (
      <div className='home'>
        <div className='home_main'>
          <div className='home_content'>
            <div className='home_content_border'>
              <div>Hey, {this.props.username}</div>
              <div>
                Welcome to the world of Mugusers. Connect with Mugusers from
                accross the world.
              </div>
              <div>1 POST = 1 MUGU</div>
              <div>
                Share Mugus that focus on your interests and Help the community
                with your insight and understanding.
              </div>
              <div> Happy Muguing</div>
            </div>
          </div>

          <div className='home_main_sidebar'>
            <h3>Treding Mugus</h3>
          </div>
        </div>
        <div className='home_footer'>Footer</div>
      </div>
    );
  }
}

export default Home;
