import React, { Component } from "react";
import "./createpost.css";

class CreatePost extends Component {
  state = {
    Heading: "Enter Heading of the Post",
    Post_Content: "Type Your Post Here",
    Author: this.props.id,
    productImage: null,
    success_message:String
  };

  handlesubmit = (e) => {
    e.preventDefault();

    const form1 = new FormData();
    form1.append("Heading", this.state.Heading);
    form1.append("Post_Content", this.state.Post_Content);
    form1.append("Author", this.state.Author);
    form1.append("productImage", this.state.productImage);
    
    fetch("/create/createpost", {
      method: "POST",
      body: form1,
    }).then((data) => {
      console.log(data.status)
      if (data.status ===200){
        this.setState({
          Heading: "Enter Heading of the Post",
          Post_Content: "Type Your Post Here",
          success_message:"Post Successfully Submitted",
        })
        
      }
    }).catch(err=>{console.log(err)});
  };  

  handlechange = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
      success_message:""
    });   
  };
  
  handlechangeimage = (e) => {
    this.setState({
      [e.target.name]: e.target.files[0],
    });
  };

  render() {
    return (
      <div className='createpost'>
        <div>
          <form
            name='newform'
            onSubmit={this.handlesubmit}
            encType='multipart/form-data'>
            <div>
              <div className='container'>
                <input
                  type='text'
                  name='Heading'
                  value={this.state.Heading}
                  onChange={this.handlechange}
                />
              </div>
              <div className='container'>
                <textarea
                  name='Post_Content'
                  value={this.state.Post_Content}
                  onChange={this.handlechange}
                />
              </div>
              <div className='container'>
                <input
                  type='file'
                  name='productImage'
                  onChange={this.handlechangeimage}
                />
              </div>
            </div>
            <div className='container'>
               <p>{this.state.success_message}</p>
            </div>
            <div className='container'>
              <input type='submit' />
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default CreatePost;
