import React, { Component } from "react";
import { Link, Route, Redirect, BrowserRouter } from "react-router-dom";
import "./login.css";

class Login extends Component {
  state = {
    username: "",
    password: "",
  };

  handlelogin = (e) => {
    e.preventDefault();
    fetch("/user/login", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        username: this.state.username,
        password: this.state.password,
      }),
      credentials: "same-origin",
    })
      .then((res) => res.json())
      .then((data) => {
        this.props.handlelogin(data);
      }).catch(err=>{console.log(err)});
  };

  handlechange = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };

  render() {
    if (!this.props.isauthenticated) {
      return (
        <div className='login'>
          <h2 className='login_text'> Login Here</h2>
          <form className='login_form' onSubmit={this.handlelogin}>
            <input
              className='login_username'
              type='text'
              name='username'
              placeholder='Enter your Username'
              onChange={this.handlechange}
            />

            <input
              className='login_password'
              type='text'
              name='password'
              placeholder='Enter your password'
              onChange={this.handlechange}
            />
            <input type='submit' />
          </form>
          <div className='Not_registered'>
            <div>
              If not Registered <Link to='/register'>Click Here</Link> to
              Register First
            </div>
          </div>
        </div>
      );
    } else {
      return <Redirect to='/home' />;
    }
  }
}

export default Login;
